
  
/* Ana container, içerikleri yan yana sıralamak için flex-direction: row kullanıyoruz */
.container-dash {
  display: flex;
  flex-direction: row; /* İçerikleri yan yana sıralar */
  width: 100%;
  height: 100vh; /* Yüksekliği tüm ekranı kaplayacak */
  margin: 0px !important;
  padding: 0px !important;
}
  
/* Sol menü kısmı, sabit kalacak */
.dashboard-container {
  width: 300px;
  height: 100vh; /* Sayfanın tamamını kaplayacak */
  border: 1px solid rgba(230, 230, 230, 1);
  box-sizing: border-box;
  transition: width 0.3s ease;
}

/* Menü daraltıldığında genişlik azalır */
.dashboard-container.collapsed {
  width: 100px;
}

/* Sağdaki içerik, sol menünün sağında yer alacak */
.dashboard-container-2 {
  justify-content: center ;
  /* height: 100%;  */
  display: block;
}
  
  .hera-logo-dashboard {
    width: 300px;
    border: 1px solid rgba(230, 230, 230, 1);
    height: 60px;
    box-sizing: border-box;
    text-align: center;
    align-items: center;
  }

  .hera-logo-dashboard.collapsed{
    width: 100px;
  }

  .img-menu.collapsed{
    margin-left: 0px;
  }
  
  .img-hera-logo {
    width: 113px;
    height: 33px;
    /* margin-left: 20px; */
    margin-top: 10px;
  }
  
  .img-menu {
    width: 31px;
    height: 31px;
    margin-left: 100px;
    margin-top: 15px;
    cursor: pointer;
  }
  
  .dashboard-menu {
    width: 300px;
    transition: width 0.3s ease;
}

.dashboard-menu.collapsed {
    width: 80px; /* Menü daraltıldığında genişliği */
}
  
  .section {
    margin-left: 30px;
    width: 250px;
    height: 50px;
    cursor: pointer;
    display: flex; /* Flexbox kullanımı */
    align-items: center; /* Dikeyde hizalama */
}

  
.dashboard-img {
    width: 30px;
    height: 30px;
}
  
.dashboard-text {
    margin-left: 20px;
    font-size: 15px;
    font-weight: 500;
    color: rgba(66, 66, 66, 1);
    line-height: normal; /* Flexbox ile hizalama sağlandığından line-height kaldırıldı */
}

.dashboard-menu.collapsed .dashboard-text {
    display: none; /* Daraltıldığında metinler gizlenir */
}

.container-right{
  width: 100%;
}
  
  .active {
    color: rgba(97, 174, 54, 1);
  }
  
/* Banner kısmı */
.dashboard-banner {
  position: relative;
  height: 60px;
  width: 100%;
  background-color: antiquewhite;
  /* box-sizing: border-box; */
}

.banner-img {
  width: 100%; 
  height: 100%; 
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}
  
  .dashboard-header {
    position: absolute;
    font-size: 20px;
    font-weight: 700;
    line-height: 60px;
    top: 0px;
    left: 20px;
    color: rgba(255, 255, 255, 1);
  }
  
  .lang-container {
    width: 65px;
    height: 36px;
    position: absolute;
    right: 150px;
    top: 10px;
    background: rgba(255, 255, 255, 1);
    border-radius: 5px;
    cursor: pointer;
  }
  
  .lang-text {
    width: 30px;
    font-size: 15px;
    font-weight: 500;
    color: rgba(66, 66, 66, 1);
    line-height: 36px;
    margin-left: 5px;
    display: inline-block;
  }

  .lang-ok{
    margin-left: 5px;
  }
  
  .profile-active {
    position: absolute;
    width: 20px;
    height: 20px;
    right: 100px;
    top: 18px;
  }
  
  .profile-name {
    position: absolute;
    color: rgba(255, 255, 255, 1);
    width: 70px;
    height: 45px;
    right: 20px;
    top: 15px;
  
  }
  
  .dash-container-img {
    width: 400px;
    height: 100px;
    top: 112px;
    left: 333px;
    gap: 0px;
    border-radius: 8px 0px 0px 0px;
    opacity: 0px;
  
  }
  
  .dash-section-2 {
    float: left;
    width: 400px;
    height: 100px;
    font-size: 15px;
    position: relative;
    margin-top: 30px;
    margin-left: 30px;
    cursor: pointer;
    color: rgba(66, 66, 66, 1);
  }
  
  .dash-section-2:hover{
    color: rgba(97, 174, 54, 1);
    font-size: 18px;
    font-weight: 700;
  }
  
  .dash-icon-2 {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 30px;
    left: 40px;
  }
  
  .dash-text-2 {
    position: absolute;
    top: 0px;
    line-height: 100px;
    left: 100px;
    
  
  }
  
  .ok{
    position: absolute;
    top: 40px;
    width: 7px;
    height: 14px;
    left: 350px;
  }