.fullscreen-div {
    display: flex;
    width: 100vw;
    height: 100vh;
}

.div-60 {
    width: 60%;
    height: 100%;
    background-color: lightblue;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
}

.div-40 {
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.responsive-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.col-20 {
    height: 20%;
    display: flex;
    color: white;
    font-size: 24px;
}

.col-70 {
    height: 70%;
    display: flex;
    align-items: center;
    color: white;
    font-size: 24px;
}

.col-10 {
    height: 10%;
    display: flex;
    color: white;
    font-size: 24px;
}

.logo {
    width: 137.04px;
    height: 40.2px;
    margin-top: 52px;
    margin-left: 74px;
    gap: 0px;
    /* opacity: 0px; */

}

.login-container{
    width: 420px;
    height: 400px;
    margin-left: 74px;
}

.sign-in-text {
    font-size: 30px;
    gap: 0px;
    /* opacity: 0px; */
    font-weight: 700;
    line-height: 36.34px;
    color: rgb(10, 10, 10, 1);
}

.enter-email {
