}

.login-button{
    width: 430px;
    height: 54.56px;
    top: 621px;
    left: 74px;
    gap: 0px;
    border-radius: 10.15px;
    /* opacity: 0px; */
    background: rgba(97, 174, 54, 1);
    box-shadow: 0px 2.55px 20.36px 0px rgba(6, 51, 54, 0.1);
    text-align: center;
    justify-content: center;
    line-height: 54.56px;
    font-size: 17px;
    font-weight: 400;
    margin-top: 20px;
    cursor: pointer;
}

.username-text {
    height: 20px;
    gap: 0px;
    /* opacity: 0px; */
    font-size: 15.23px;
    font-weight: 600;
    line-height: 19.79px;
    color: rgba(10, 10, 10, 1);
}

.username-input {
    width: 416px;
    height: 50px;
    gap: 0px;
    border-radius: 10.15px;
