.hardware {
    position: relative;
    width: 90%;
    height: 400px;
    margin-left: 50px;
}

.header {
    margin-top: 20px;
    font-size: 15px;
    font-weight: 500;
    display: inline-block;
}

.input {
    width: 100%;
    height: 40px;
    gap: 0px;
    border-radius: 8px;
    border: 1px 0px 0px 0px;
    opacity: 0px;
    border: 1px solid #D9D9D9;
    margin-top: 10px;
    padding-left: 10px;
    color: #818181;
    box-sizing: border-box;
}

.input:disabled {
    background-color: #f0f0f0;
    /* Daha soluk bir arka plan rengi */
    border-color: #ccc;
    /* Gri kenar */
    color: #999;
    /* Metin rengini soluklaştır */
    cursor: not-allowed;
    /* Fare imlecini 'not-allowed' yap */
}

.switch {
    position: absolute;
    display: inline-block;
    width: 53px;
    height: 25px;
    right: 0px;
    margin-top: 20px;
}

/* Checkbox'ı gizlemek için */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* Kaydırma çubuğu */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 25px;
}

/* Kaydırma çubuğunun daire şekli */
.slider:before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

/* Toggle açık olduğunda arka plan yeşil olur */
input:checked+.slider {
    background-color: green;
}

/* Toggle açık olduğunda daire sağa kayar */
input:checked+.slider:before {
    transform: translateX(26px);
}

/* Yuvarlak kenarlı kaydırma */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.save-button {
    width: 100%;
    height: 40px;
    margin: auto;
    border-radius: 10px;
    box-shadow: 0px 2.55px 20.36px 0px rgba(6, 51, 54, 0.1);
    font-size: 16px;
    line-height: 40px;
    color: #FFFFFF;
    text-align: center;
    cursor: pointer;
    background: #60A511;
    margin-top: 30px;
}

.save-button:hover {
    box-shadow: 10px 10px 20.36px 0px rgba(26, 29, 27, 0.5);
}