



.charge-container {
    display: flex ;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 400px; 
    width: 1100px;
    margin-top: 50px;
    margin-left: 100px;
}

.charge-item-1 {
    width: 20%; /* İsteğe bağlı genişlik */
    height: 100%;
}

.charge-item-2 {
    width: 60%; /* İsteğe bağlı genişlik */
    text-align: center; /* İçerikleri ortala */
    height: 100%;
    align-items: center;
}

.charge-item-3 {
    width: 10%; /* İsteğe bağlı genişlik */
    height: 100%;
}

.start-date-header{
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 10px;
    line-height: 30px;
}

.text-header{
    font-size: 15px;
    font-weight: 500;
    line-height: 30px;
}

.weight-700{
    font-weight: 700;
}

.charge-button{
    width: 800px;
    height: 60px;
    margin: auto;
    border-radius: 20px;
    box-shadow: 0px 2.55px 20.36px 0px rgba(6, 51, 54, 0.1);
    font-size: 20px;
    line-height: 60px;
    color: #FFFFFF;
    text-align: center;
    cursor: pointer;
}

.charge-button:hover{
    box-shadow: 10px 10px 20.36px 0px rgba(26, 29, 27, 0.5);
}